* {
  font-family: "Poppins", sans-serif;
}

.CardContainer {
  height: 69rem;
  width: 35rem;
  /* border: 1px solid black; */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  padding: 1rem 1.5rem;
  border-radius: 1rem;
}

.header {
  display: flex;
  justify-content: space-between;
  font-size: 2rem;
}

.ButtonContainer {
  display: flex;
  justify-content: space-evenly;
  padding: 1.5rem 0;
}

.text {
  font-size: 2rem;
  margin-left: 2rem;
}

.ButtonContainer button {
  padding: 1rem;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  color: black;
  border-radius: 0.5rem;
  background-color: rgb(255, 255, 255);
  width: 14rem;
  text-align: center;
  font-weight: bold;
}

.ButtonContainer #btn-1 {
  padding: 1rem 4.5rem;
  border: none;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  color: white;
  border-radius: 0.5rem;
  background-color: #2596be;
  width: 14rem;
}

.bodyComponent {
  border: 1px solid black;
  border-radius: 2rem;
  padding: 2rem 1rem;
}

.colored {
  background-color: rgb(233, 200, 86);
  color: white;
}

.selectLocations select {
  border: none;
  font-size: large;
  padding-left: 2rem;
  padding-top: 1rem;
  font-weight: 500;
  border-radius: 5px;
  font-family: "Poppins", sans-serif;
  padding-bottom: 1rem;
  outline: none;
}

option {
  background-color: #2596be;
  color: white;
}

option:hover {
  background-color: #2596be;
  color: white;
}

.RangePicker {
  border: none;
}

.border {
  border-bottom: 1px solid black;
}

.totalPriceContainer {
  display: flex;
  justify-content: space-between;
}

.footer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 1.5rem 0;
}

.footer button {
  border: none;
  padding: 1rem 7rem;
  border-radius: 0.5rem;
  background-color: #2596be;
  color: whitesmoke;
  font-size: 2rem;
  font-weight: 500;
  text-align: center;
}

.dateInput input {
  border: none;
  padding: 1rem 3rem;
  margin-top: 1rem;
  margin-bottom: 1rem;
  font-size: 16px;
  font-family: "Poppins", sans-serif;
  font-weight: 500;
  outline: none;
}

.totalPriceContainer {
  font-size: 2rem;
  font-weight: 600;
  margin: 1rem 0;
}

.inquiryComponent {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.inputFields {
  padding: 1rem 0;
  width: 100%;
}

.inputFields input {
  width: 100%;
  padding: 1rem;
  border-radius: 0.5rem;
  margin-top: 1rem;
}

.inputFields button {
  border: none;
  padding: 1rem 2.5rem;
  background-color: #2596be;
  border-radius: 0.5rem;
  color: white;
  font-weight: bold;
  width: 20rem;
  text-align: center;
  margin-left: 7rem;
  margin-top: 3rem;
}

.picker-text {
  margin-left: 2rem;
}

.Price-Container {
  font-weight: 500;
}

.error {
  color: red;
  margin-left: 5px;
  font-size: small;
  margin-top: 5px;
}

.whiteClass {
  color: white;
}
